* {
  padding: 0;
  margin: 0;
}

.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

ul {
  list-style: none;
}

.tutorial-container {
  background-color: #182e32;
  border-radius: 5px;
  border: 2px #e2ddb8 solid;
  position: fixed;
  width: 20rem;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 99;
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: 1fr 3fr;
      grid-template-rows: 1fr 3fr;
  gap: 2px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.tutorial-container span {
  text-transform: uppercase;
  color: #ff5f7a;
}

.tutorial-container p {
  color: #fff;
  text-align: left;
  margin-block: 0.5rem;
}

.tutorial-container .tutorial-description {
  padding: 1rem;
  position: relative;
}

.tutorial-container .tutorial-description::after {
  content: "";
  background-color: var(--clr-primary);
  width: 90%;
  height: 1px;
  position: absolute;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.tutorial-container .tutorial-examples {
  padding: 1rem;
  display: -ms-grid;
  display: grid;
  gap: 1rem;
}

.tutorial-container .tutorial-examples .example-word {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 2px;
}

.tutorial-container .tutorial-examples .example-word .word-correct {
  background-color: #67be61;
  -webkit-box-shadow: 0 0 2px #67be61;
          box-shadow: 0 0 2px #67be61;
}

.tutorial-container .tutorial-examples .example-word .word-close {
  background-color: #efcebb;
  -webkit-box-shadow: 0 0 2px #efcebb;
          box-shadow: 0 0 2px #efcebb;
}

.tutorial-container .tutorial-examples .example-word .word-incorrect {
  background-color: #e4e2dc;
  -webkit-box-shadow: 0 0 2px #e4e2dc;
          box-shadow: 0 0 2px #e4e2dc;
}

.tutorial-container .tutorial-examples .example-word > div {
  display: -ms-grid;
  display: grid;
  place-content: center;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 1.3rem;
  background-color: rgba(255, 255, 255, 0.5);
  border: 1px #000 solid;
  border-radius: 5px;
}

.tutorial-container .btn {
  background-color: var(--clr-primary);
  border: 1px black solid;
  padding: 0.5rem;
  color: #182e32;
  font-size: 1.3rem;
  cursor: pointer;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}

.tutorial-container .btn:hover {
  background-color: #efcebb;
  -webkit-box-shadow: 0 0 5px #efcebb;
          box-shadow: 0 0 5px #efcebb;
}

.tut-container {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  inset: 0;
  z-index: 88;
}
